
import Home from "../home/home.vue";
import Category from "../category/category.vue";
import Infant from "../infant/infant.vue";
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
name: "index",
components: { Home, Category, Infant, Tabbar, TabbarItem },
data() {
return {
tabbarIndex: 0
};
},
computed: {},
watch: {},

methods: {
tabbarItemClick(tabbarIndex) {
this.tabbarIndex = tabbarIndex;
}
},
created() {},
mounted() {}
};



